	  function Cookie(name, value, options) {
	    options = options || {};
	
	    if (typeof value === 'undefined') {
	      return getCookie(name);
	      // } else if (value === null) {
	    } else if (value === null || value === '') {
	        return clearCookie(name, options);
	      } else {
	        return setCookie(name, value, options);
	      }
	
	    function setCookie(name, value, options) {
	      var expires = options.expires;
	      var path = options.path;
	      var domain = options.domain;
	      var secure = options.secure;
	
	      // 
	      if (typeof expires === 'number') {
	        // expires = new Date(new Date().getTime() + expires * 1000 * 60 * 60); // 
	        // expires = new Date(new Date().getTime() + expires * 1000 * 60 * 60 * 24); // 
	        expires = new Date(new Date().getTime() + expires * 864e+5); // 
	      }
	      // if (typeof expires === 'number') {
	      //   expires = new Date();
	      //   expires.setDate(expires.getDate() + options.expires); // 
	      // }
	
	      // document.cookie = name + '=' + escape(value) + ((expires) ? '; expires=' + expires.toGMTString() : '') + ((path) ? '; path=' + path : '; path=/') + ((domain) ? ';domain=' + domain : '');
	      document.cookie = name + '=' + escape(value) + (expires ? '; expires=' + expires.toUTCString() : '') + (path ? '; path=' + path : '') + (domain ? '; domain=' + domain : '') + (secure ? '; secure' : '');
	
	      return true;
	    }
	
	    function getCookie(name) {
	      var arr = document.cookie.match(new RegExp('(^| )' + name + '=([^;]*)(;|$)'));
	      if (arr !== null) {
	        return unescape(arr[2]);
	      }
	
	      // return null;
	      return '';
	    }
	
	    function clearCookie(name, options) {
	      // document.cookie =
	      //   name + '=' +
	      //   (options.path ? '; path=' + options.path : '') +
	      //   (options.domain ? '; domain=' + options.domain : '') +
	      //   '; expires=Fri, 02-Jan-1970 00:00:00 GMT';
	
	      options.expires = new Date(0);
	      return setCookie(name, '', options);
	    }
	  }
	
